<template>
  <div class="game">
    <div class="top-bar">
      <div class="dual-container">
        <fading-message duration="-1" v-bind:trigger="visibleElapsedTime">
          <div class="bigger">Time wasted</div>
          <i class="fas fa-hourglass-end"></i>
          <duration :amount="elapsedTime"></duration>
        </fading-message>
        <div class="levels">
          <fading-message duration="-1" v-bind:trigger="visibleLevels" class="bigger">
            Level <span class="highlight">{{step + 1}}</span>
          </fading-message>
          <fading-message duration="-1" v-bind:trigger="visibleNextDuration">
            <duration :amount="nextAmountToWait"></duration>
            <i class="fas fa-hourglass-start"></i>
          </fading-message>
        </div>
      </div>
    </div>
    <div class="center-bar">
      <div class="even-bigger">
        <!-- <span class="dots-compensator" v-if="visibleDots"></span> -->
        <animated-dots nbDots="3" v-bind:invisibleDots="!visibleDots" v-bind:invisibleReverseDots="!visibleReverseDots">
           Wait for <duration :amount="cooldown" fallback="it"></duration>
        </animated-dots>
      </div>
    </div>
    <div class="bottom-bar">
      <div class="taunt">
        <fading-message duration="3" v-bind:trigger="visibleTaunt">
          {{tauntMessage}}
        </fading-message>
      </div>
    </div>
  </div>
</template>

<script>
import FadingMessage from './FadingMessage.vue'
import Duration from './Duration.vue'
import AnimatedDots from './AnimatedDots.vue'
export default {
  name: 'game',
  components: { FadingMessage, Duration, AnimatedDots },
  data () {
    return {
      developerMode: false,
      elapsedTime: 0,
      step: 0,
      tauntMessage: '',
      visibleElapsedTime: false,
      visibleLevels: false,
      visibleTaunt: false,
      visibleDots: false,
      visibleReverseDots: false,
      visibleNextDuration: false,
      steps: {
        0: () => { /* this.taunt('It\'s gonna be good') */ },
        1: () => { this.taunt(`Congrats ! You just wasted ${this.elapsedTime} seconds`) },
        2: () => { this.visibleElapsedTime = true; this.taunt('Got nothing else to do, eh ?') },
        3: () => { this.visibleLevels = true; this.taunt(`You're level ${this.step + 2} now, well played !`) },
        4: () => { this.visibleNextDuration = true; this.taunt('You can now see the duration of the current level') },
        5: () => { this.visibleDots = true; this.taunt('Here, have some animated dots') },
        6: () => { this.taunt('Are you entertained ?') },
        7: () => { this.visibleReverseDots = true; this.taunt('Synchronized reversed dots ?! Wow O_O') },
        default: () => { this.taunt('LOL, you\'re still here ?') }
      }
    }
  },
  computed: {
    nextAmountToWait () {
      return this.totalAmountToWait(this.step) - this.totalAmountToWait(this.step - 1)
    },
    cooldown () {
      return this.totalAmountToWait(this.step) - this.elapsedTime
    }
  },
  methods: {
    totalAmountToWait (givenStep) {
      if (givenStep < 0) {
        return 0
      } else {
        if (this.developerMode) {
          return 5 * givenStep
        } else {
          return 5 * givenStep * givenStep
        }
      }
    },
    countDown () {
      this.computeGame()
      setInterval(() => {
        this.elapsedTime++
        this.computeGame()
      }, 1000)
    },
    computeGame () {
      if (this.cooldown <= 0) {
        let stepMethod = this.steps[this.step]
        if (stepMethod) {
          stepMethod()
        } else {
          this.steps.default()
        }
        this.step++
      }
    },
    taunt (message) {
      if (message) { this.tauntMessage = message }
      this.visibleTaunt = !this.visibleTaunt
    }
  },
  mounted () {
    this.countDown()
  }
}
</script>

<style scoped>
.game {
  flex-grow: 1;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  padding: 15px;
}
.dual-container {
  display: flex;
  justify-content: space-between;
}
.top-bar div {
  margin-bottom: 5px;
}
.top-bar, .bottom-bar {
  height: 10em;
  width: 100%;
}
.center-bar {
  text-align: center;
}
.taunt {
  margin-top: 10px;
  text-align: center;
  /* color: #dfe9f3; */
}
.dots-compensator {
  width: 3em;
  width: 1em;
  height: 1em;
  float: left;
}
.levels {
  text-align: right;
  white-space: nowrap;
}
.bigger {
  font-size: 1.25em;
}
.even-bigger {
  font-size: 1.5em;
}
.fa-hourglass-end {
  margin-left: 5px;
}
.fa-hourglass-start {
  margin-right: 5px;
}
</style>



